html,
body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
}

@font-face {
    font-family: "DINPro-Light";
    src: url("../fonts/DINPro-Light\ tr.ttf");
}

* {
    font-family: "DINPro-Light";
}

.main {
    height: inherit;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-content: space-between;
}

.header {
    display: flex;
    flex-wrap: wrap;
}

.header > section {
    flex: 1 0 35%;
    margin: 5px;
    height: 100px;
}

.intro {
    font-size: 3vh;
    text-align: right;
    padding-right: 10vh;
}

a {
    color: black;
    text-decoration: none;
}

.location {
    margin-right: auto;
    position: fixed;
    z-index: 1000000;
    transform: rotate(-90deg);
}

.nav {
    display: flex;
    justify-content: space-between;
    margin-top: auto;
    position: fixed;
    z-index: 1000000;
    overflow: hidden;
    bottom: 0px;
    width: 100%;
}

.nav ul {
    list-style-type: none;
}

.nav__socials {
}

.nav__menu {
    margin-right: 5vh;
    text-align: right;
}
